<script>
    import { fly } from 'svelte/transition';
    import {onMount} from 'svelte';

    var itemx = [
        { 
            t: 'Petskita (mobile)', 
            i: './img/project/petskita-mobile.jpg', 
            d: 'E-Commerce app that run on mobile/web, used to sell all animal needs.',
            b: 'React Native, UI Kitten'
        },
        { 
            t: 'Digital Cargo (mobile)', 
            i: './img/project/dika-mobile.jpg', 
            d: 'Expedition app that run on mobile/web, used to order trucks and track these trucks.',
            b: 'React Native'
        },
        { 
            t: 'Digital Cargo', 
            i: './img/project/dika-web.jpg', 
            d: 'Expedition app that run on mobile/web, used to order trucks and track these trucks.',
            b: 'PHP, Bootstrap, JQuery'
        },
        { 
            t: 'My portofolio', 
            i: './img/project/portofolio.jpg',
            d: 'Profile web that run on web, used to introduce my self using a media website to make people know more about me.',
            b: 'Svelte JS, Pure CSS'
        },
        { 
            t: 'Smart SMK Mandiri', 
            i: './img/project/smk-mandiri.jpg',
            d: 'Academic system app that run on web, used to handle the academic and financial needs of the school.',
            b: 'PHP, Bootstrap, JQuery'
        },
        { 
            t: 'Prestise', 
            i: './img/project/prestisee-mobile.jpg',
            d: 'Real estate app that run on mobile, used to sell various kinds of shelter needs. (Just design)',
            b: 'React Native'
        },
        { 
            t: 'E-Library SMK Mandiri',
            i: './img/project/elibrary-mandiri.jpg',
            d: 'Library app that run on web, used to show list book and stock book on school libary.',
            b: 'PHP, JQuery'
        },
        { 
            t: 'E-ICJS Deli Serdang',
            i: './img/project/eicjsds.jpg',
            d: 'Library app that run on web, used to show list book and stock book on school libary.',
            b: 'PHP, JQuery'
        }
    ];

    var delay = 400;
    var counter = 0;

    onMount(() => {
        var items = []
  , point = document.querySelector('svg').createSVGPoint();

    function getCoordinates(e, svg) {
    point.x = e.clientX;
    point.y = e.clientY;
    return point.matrixTransform(svg.getScreenCTM().inverse());
    }

    function changeColor(e) {
    document.body.className = e.currentTarget.className;
    }

    function Item(config) {
        Object.keys(config).forEach(function (item) {
            this[item] = config[item];
        }, this);
        this.el.addEventListener('mousemove', this.mouseMoveHandler.bind(this));
        this.el.addEventListener('touchmove', this.touchMoveHandler.bind(this));
    }

    Item.prototype = {
        update: function update(c) {
            this.clip.setAttribute('cx', c.x);
            this.clip.setAttribute('cy', c.y);
        },

        mouseMoveHandler: function mouseMoveHandler(e) {
            this.update(getCoordinates(e, this.svg));
        },

        touchMoveHandler: function touchMoveHandler(e) {
            e.preventDefault();
            var touch = e.targetTouches[0];
            if (touch) return this.update(getCoordinates(touch, this.svg));
        }
    };

    [].slice.call(document.querySelectorAll('.item'), 0).forEach(function (item, index) {
        items.push(new Item({
            el: item,
            svg: item.querySelector('svg'),
            clip: document.querySelector('#clip-'+index+' circle'),
        }));
    });

    [].slice.call(document.querySelectorAll('button'), 0).forEach(function (button) {
        button.addEventListener('click', changeColor);
    });
    })

</script>
<div class="work-container">
    <div class="col-10">
        <div class="text-container">
            <div class="title-container">
                <h1>Work and project</h1>
            </div>
        </div>
        <div class="grid-list">
            <div class="items">
                {#each itemx as item, i}
                    <div class="item" in:fly="{{y: 400, duration: 600, delay: 400+(i*100)}}">
                        <svg>
                            <defs>
                                <clipPath id="clip-{i}">
                                    <circle cx="0" cy="0" r="150px" fill="#000"></circle>
                                </clipPath>
                            </defs>
                            <text class="svg-text" x="50%" y="50%" dy=".3em">{item.t}</text>
                            <g clip-path="url(#clip-{i})">
                                <image xlink:href="{item.i}" width="100%" height="100%"  transform="translate(-35.5,-31)" preserveAspectRatio="xMinYMin slice"></image>
                                <text class="svg-masked-text" x="50%" y="90%" dy=".3em">{item.b}</text>
                            </g>
                        </svg>
                    </div>
                {/each}
            </div>
        </div>
    </div>
</div>