<script>

    import { createEventDispatcher } from 'svelte';
    import { elasticOut } from 'svelte/easing';
    const dispatch = createEventDispatcher();
    
    export let message;

    function closeAlert(){
        dispatch('triggeralert', {
			show: false
		});
    }

    function fade(node, {
        delay = 100, // 0 ms before the transition begins
        duration = 1000 // Transition lasts for 400 ms
        }) {
        return {
            delay,
            duration,
            css: t => {
            const eased = elasticOut(t);
            return `
                transform: scale(${eased});
            `
            }
        };
	}
</script>

<div class="alert-container" on:click={closeAlert}>
    <div in:fade class="message-container">
        <div class="alert-title-container">
            <p>Alert Message</p>
        </div>
        <div class="alert-msg-container">
            <p>{message}</p>
        </div>
        <div class="btn-container">
            <a href="#/" class="alert-button" on:click={closeAlert}>DONE</a>
        </div>
    </div>
</div>