<script>
	import { onMount } from 'svelte';
	import Home from './screen/Home.svelte';
	import About from './screen/About.svelte';
	import Skills from './screen/Skills.svelte';
	import Work from './screen/Work.svelte';
	import Contact from './screen/Contact.svelte';
	import Loading from './loading.svelte';
	import Alert from './alert.svelte';

	var loading = false;
	var show = true;
	var msg = "This is alert.";
	var alert = false;

	var pageName = '';

	function changePage(name){
		alert = false;
		loading = true;
		pageName = '';
		setTimeout(()=> {loading = false; pageName = name;}, 2100);
	}

	function triggerAlert(show, message){
		if(show){
			msg = message;
		}
		alert = show;
	}

	function fade(node, {
        delay = 0, // 0 ms before the transition begins
        duration = 600 // Transition lasts for 400 ms
        }) {
        const o = +getComputedStyle(node).opacity;
        return {
            delay,
            duration,
            css: t => `
                opacity: ${t * o};
                transform: scale(${t >= 0.9 ? t : 0.9});
            `
        };
	}
	
	function toggleMenu() {
		var element = document.getElementsByClassName("hamburger-menu");
		for (var i = 0; i < element.length; i++) {
            element[i].classList.toggle('animate');
		}
		var elements = document.getElementsByClassName("navbar-menu");
		for (var i = 0; i < elements.length; i++) {
            elements[i].classList.toggle('active-menu');
		}
	}

	onMount(() => {
		pageName = "home";
	})
</script>

