<script>

    import { createEventDispatcher, onMount } from 'svelte';

    const dispatch = createEventDispatcher();
    
    function handleChangepage() {
        dispatch('changePage', {
			page: 'contact'
		});
    }

	function spin(node, { duration, delay = 650 }) {
		return {
            delay,
			duration,
			css: t => {
				return `
                    transform: rotate(${t * 360}deg);
                `
			}
		};
	}

    function dash(node, { duration = 300, delay = 650, n = 0 }) {
		return {
			duration,
            delay,
			css: t => {
				return `
					transform: translate(${(n-t)*35}px, -${(n-t)*25}px)
				`;
			}
		};
    }
    
</script>

<div class="home-container">
    
    <div class="content-container">
        <div class="text-container">
            <div class="title-container">
                <h1 class="title">Hi, <br/> I'm <div class="bg-secondary" in:spin="{{duration: 500}}" >Han</div> , <br/>fullstack developer.</h1>
            </div>
            <h2>Backend Developer / Mobile Developer / Freelancer</h2>
            <div class="button-container">
                 <a href="#/" on:click={handleChangepage} class="btn-primary">CONTACT ME</a>
            </div>
        </div>
    </div>

    <div class="image-container">
        <img class="main-image" src="./img/Final2.png" alt="Picture" aria-hidden="true"/>

        {#each new Array(10) as _, i}
            <img class="outline-image" id="outline-{i+1}" in:dash="{{number: i, delay: ((i+1)*30)+800}}" style="z-index: {15-i}; transform: translate({(i+1)*37}px, -{(i+1)*19}px);" src="./img/Outline-Profile.png" alt="Picture" aria-hidden="true"/>
        {/each}

    </div>

</div>