<script>
    import { fly } from 'svelte/transition';
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();

    function showAlert(msg){
        dispatch('triggeralert', {
            show: true,
            message: msg
		});
    }

    function validateEmail(email) {
        const re = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        return re.test(email);
    }

    var name = '';
    var email = '';
    var subject = '';
    var msg = '';

    function sendEmail(){
        if(name == ''){
            showAlert("Name cannot be empty.");
        }else if(email == ''){
            showAlert("Email cannot be empty.");
        }else if(subject == ''){
            showAlert("Subject cannot be empty.");
        }else if(msg == ''){
            showAlert("Message cannot be empty.");
        }else{
            Email.send({
                SecureToken : "dcf0c947-32b4-4030-b4a6-4d984311d2ee",
                To : 'jitufarhan2@gmail.com',
                From : email,
                Subject : name +" - "+ subject,
                Body : msg
            }).then(
                message => {
                    showAlert("Your email has sended.");
                    name = '';
                    email = '';
                    subject = '';
                    msg = '';
                }
            );
        }
    }
</script>

<svelte:head>
    <script src="https://smtpjs.com/v3/smtp.js">
</script>

</svelte:head>
<div class="contact-container">
    <div class="col-4">
        <div class="text-container">
            <div class="title-container">
                <h1>Contact me</h1>
            </div>
            <p>I am interested in freelance opportunities – especially ambitious or large projects. However, if you have other request or question, don’t hesitate to contact me using below form either.</p>
            <div class="input-container">
                <div class="half" in:fly="{{y: 100, duration: 600, delay: 400}}">
                    <div class="input-name">
                        <input type="text" bind:value={name} placeholder="Name">
                        <span class="underline-animation"></span>
                    </div>
                </div>
                <div class="half" in:fly="{{y: 100, duration: 600, delay: 400}}">
                    <div class="input-name">
                        <input type="text" bind:value={email} placeholder="Email">
                        <span class="underline-animation"></span>
                    </div>
                </div>
                <div class="full" in:fly="{{y: 100, duration: 600, delay: 600}}">
                    <div class="input-name">
                        <input type="text" bind:value={subject} placeholder="Subject">
                        <span class="underline-animation"></span>
                    </div>
                </div>
                <div class="full" in:fly="{{y: 100, duration: 600, delay: 800}}">
                    <div class="input-name">
                        <textarea name="" id="" bind:value={msg} rows="5" placeholder="Message"></textarea>
                        <span class="underline-animation"></span>
                    </div>
                </div>
                <div class="full" in:fly="{{y: 100, duration: 600, delay: 1000}}">
                    <div class="button-container">
                        <a href="#/" on:click={sendEmail} class="btn-primary pull-right">SEND MESSAGE</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="col-6">
    
    </div>
</div>